.App {
  text-align: center;
  padding-top: 30px;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.admin {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  width: 150px;
}

.create-task {
  height: 50px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: #00C2FF;
  color: #fff;
  border: none;
  width: 200px;
}

.body {
  padding-top: 30px;
}

.body-header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
}

.body-header button {
  background-color: #fff;
  border: none;
}

.body-tasks-item {
  padding: 35px;
  border: 1px solid #00C2FF;
  border-radius: 20px;
  margin-top: 35px;
}

.body-tasks-item textarea {
  padding: 0;
  border: none;
  resize: none;
}

.body-tasks-item-name,
.body-tasks-item-email,
.body-tasks-item-text {
  text-align: left;
  margin-top: 10px;
}

.body-task-status {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.body-tasks-item-name {
  margin-top: 0;
}

.pagination {
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 40px;
}
.status button,
.email button,
.username button {
  padding: 0;
  margin-left: 10px;
}

.status button i,
.email button i,
.username button i {
  display: -webkit-flex;
  display: flex;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

